* {
  box-sizing: border-box;
}

:root{
  background-color: #4D2D52;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #C1A5A9
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App {
  text-align: center;
}

header{
  padding: 20px;
  background-color: #1D1A31 !important;
  display: flex;
  /*ensures that the name and the menu is seperated*/
  justify-content: space-between;
  position:sticky;
}
#footer{
  padding: 20px;
  background-color: #1D1A31 !important;
  display: flex;
  /*ensures that the name and the menu is seperated*/
  position:sticky;
}

.name{
  font-size: 30px !important;
}

.Resume h1,h2{
  font-size: 40px;
  font-weight: bold;
  color: #C1A549;

}
 .about h1{
  font-size: 40px;
  font-weight: bold;
  color: #C1A549;
}
.flex-row{
  border: 1px solid black;
  width: 25%;
  display: flex;
  flex-direction: column;
  margin: 5px;
}

.flex-row h1{
  margin: 5% !important;
  flex-direction: column;
}
.flex-row a{
  color:rgba(0, 0, 0, .65)!important;
  font-weight: bold !important;
  font-size: 12px;
  background-color: grey;
}
.flex-row p{
  color: black!important;
  font-weight: bold !important;
  font-size: 12x;
  padding: 0 !important;
  margin-bottom: 0;
  background-color: grey;
}
.text-box{
  background-color: grey;
}

#project{
  display: flex;
  justify-content: center;
  flex-wrap:wrap;
 
}
.color{
  color: yellow;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #C1A549;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.main {

	margin: 0 auto;
	max-width: 90%;
  padding: 50px 30px;
  background-color: #534B62;
}
:root {
  --light: #fafafa;
  --dark: #000000;
  --darker: #111111;
  --error: rgb(228, 46, 1);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
  --spacing-five: calc(2.5rem);

}
.mainPage{
padding: 20px 30px 40px;
padding-top: var(--spacing-one);
padding-bottom: var(--spacing-one);
margin-top: var(--spacing-five);
margin-bottom: var(--spacing-five);
}
.mainPage p{
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 30px 30px 60px 50px;
  /*margin-right: 100px*/
}


html,
body {
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-weight: 600;
  line-height: 1.5;
  font-size: 18px;
  letter-spacing: .1em;
  color: var(--light);
  font-family: 'Poiret One', cursive;

}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
  font-weight: bold;
  color: #C1A549;
}

h1 {
  font-size: 2.25rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1.1rem;
  margin-top: 0;
}

a:hover {
  text-decoration: underline;
}


header a {
  color: var(--light);
}

button {
  font-size: 1rem;
  text-decoration: none;
  padding: 0.25rem 1rem;
  border-radius: .5rem;
  border: none;
  outline: none;
  cursor: pointer;
  margin: 0 1px;
  color: #C1A549;
  background-color: var(--secondary);
}

button:hover {
  opacity: .8;
}

button[disabled] {
  cursor: default;
  opacity: .8;
}

span[role="img"] {
  cursor: default;
}

form {
  width: 40%;
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

input:focus {
  border: 1px solid var(--secondary);
  box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
}

li {
  list-style: none;
}

nav a:hover {
  text-decoration: none;
}

nav  li {
  cursor: pointer;
}

textarea {
	width: 600px;
	height: 120px;
	border: 3px solid #cccccc;
	padding: 5px;
}

.container {
  width: 85%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.flex-end {
  justify-content: flex-end;
}

.mx-1 {
  margin-right: var(--spacing-one);
  margin-left: var(--spacing-one);
}

.mx-2 {
  margin-right: var(--spacing-two);
  margin-left: var(--spacing-two);
}

.my-1 {
  margin-top: var(--spacing-one);
  margin-bottom: var(--spacing-one);
}

.my-2 {
  margin-top: var(--spacing-two);
  margin-bottom: var(--spacing-two);
}

.my-5 {
  margin-top: var(--spacing-five);
  margin-bottom: var(--spacing-five);
}

.px-1 {
  padding-right: var(--spacing-one);
  padding-left: var(--spacing-one);
}

.px-2 {
  padding-right: var(--spacing-two);
  padding-left: var(--spacing-two);
}

.py-1 {
  padding-top: var(--spacing-one);
  padding-bottom: var(--spacing-one);
}

.py-2 {
  padding-top: var(--spacing-two);
  padding-bottom: var(--spacing-two);
}

.card {
  width: 25%;
  text-align: center;
}

.img-thumbnail {
  width: auto;
  height: 300px;
  margin-bottom: 50px;
  cursor: pointer;
}

button {
	background: var(--primary);
	border: 0;
	border-radius: 5px;
	color: #fff;
	cursor: pointer;
	font-size: 25px;
	font-weight: 600;
	margin-bottom: 30px;
	padding: 10px 15px;	
}


.navActive {
  color: var(--secondary);
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}




